::-webkit-scrollbar {
    width: 0;  /* Remove scrollbar space */
    background: transparent;  /* Optional: just make scrollbar invisible */
}
.message-sender-container{
    text-align: end;
}
.message-sender{
    background-color: #3C7A3C;
    border-radius: 20px 20px 0 20px;
    color: white;
    padding: 0.5rem 1.25rem;
    width: auto;
    display: inline-block;
}
.message-receiver-container{
    text-align: start;
}
.message-receiver{
    background-color: white;
    border-radius: 20px 20px 20px 0;
    color: #444444;
    padding: 0.5rem 1.25rem;
    width: auto;
    display: inline-block;
}
.green-counter{
    position: absolute;
    text-align: center;
    border-radius: 50%;
    width: 25px;
    height: 25px;
    background-color: #3C7A3C;
    top: -10px;
    right: 0;
}
.green-counter p{
    font-size: 11px;
    color : white;
    padding: 0.3rem 0;
}
